        <p>É possível evitar a menstruação a fim de melhorar os sintomas da cólica, da TPM e da endometriose com o uso dos anticoncepcionais nos chamados regime contínuo (não há pausa) e regime estendido (a pausa é realizada após 84 dias)e regime flexível (pausa entre o 24° e 120° dia da pílula, de acordo com o que a mulher optar).</p>
