          <li><strong>Impactos psicológicos:</strong> a falta de conhecimento e informação reforçam certos tabus em torno da menstruação, gerando diversos tipos de preconceito. Muitas jovens têm, inclusive, vergonha de ir ao médico e desconhecem o funcionamento do próprio corpo. Para se ter uma ideia, uma pesquisa realizada pela FEBRASGO (Federação Brasileira das Associações de Ginecologia e Obstetrícia) juntamente com o Datafolha constatou que 4 milhões de mulheres no Brasil nunca foram ao ginecologista e 16,2 milhões não se examinam com o especialista há mais de um ano por vergonha, medo de algum diagnóstico ou simplesmente por não gostarem.</li>
