import React, { useState } from 'react'
import {PDFJS} from "pdfjs-dist"
import "../WebsiteRoutes/components/styles/bula.css"
import Highlighter from "react-highlight-words";


function ReadFile() {

  const [text, setText] = useState("")
  const [dataArr, setDataArr] = useState([])
  const [font, setFont] = useState(14)
  const [upload, setUpload] = useState(false)
  const [searchedWord, setSearchedWord] = useState("")
  var textRead = ""

  function ExtractText(e){
    var file_Reader = new FileReader()
    file_Reader.readAsDataURL(e.target.files[0])
    file_Reader.onloadend = function(event){
      convertURIToBinary(event.target.result)
    }
    setUpload(true)

  }

  var BASE64_MARKER = ";base64,"

  function convertURIToBinary(dataURI){
    var base64Index = dataURI.indexOf(BASE64_MARKER) + BASE64_MARKER.length;
    var base64 = dataURI.substring(base64Index);
    var raw = window.atob(base64);
    var rawLength = raw.length;
    var array = new Uint8Array(new ArrayBuffer(rawLength));

    for (var i = 0; i < rawLength; i++) {
        array[i] = raw.charCodeAt(i);
    }
    pdfAsArray(array)
  }

  function getPageText(pageNum, PDFDocumentInstance) {
    return new Promise(function (resolve, reject) {
      PDFDocumentInstance.getPage(pageNum).then(function (pdfPage) {
        pdfPage.getTextContent().then(function (textContent) {
          var textItems = textContent.items;
          var finalString = "";

          for (var i = 0; i < textItems.length; i++) {
            var item = textItems[i];

            finalString += item.str + " ";
        }

        resolve(finalString);
    });
      });
  });
        }

  function pdfAsArray(pdfAsArray){
    PDFJS.getDocument(pdfAsArray).then(function (pdf) {
    var pdfDocument = pdf;
    var pagesPromises = [];

    for (var i = 0; i < pdf.pdfInfo.numPages; i++) {
      (function (pageNumber) {
          pagesPromises.push(getPageText(pageNumber, pdfDocument));
      })(i + 1);
  }

    Promise.all(pagesPromises).then(function (pagesText) {

    setDataArr(pagesText)
    for (var pageNum = 0; pageNum < pagesText.length; pageNum++) {
        setText(pagesText[pageNum]);
    }
  });

    }, function (reason) {
        // PDF loading error
        console.error(reason);
    });
  }
  
  

  return (
    <React.Fragment>    
      
      <br></br>
      
      <input type="file" id="file-id" className={upload?"display-none":"upload-input"} name="file_name" onChange={(e)=>ExtractText(e)}>
      </input>

      <br />
      <div className="output">

      <span>
        <br /><br /><br />        
        <input type="text" value={searchedWord} onChange={(e)=>setSearchedWord(e.target.value)} className={upload?'searchforsomething':"display-none"} placeholder="Pesquisar uma informação &#x1F50E;&#xFE0F;" />
        
      </span>

      <abbr title={font}><input type="range" className={upload?'range-input':"display-none"} min={14} defaultValue={20} max={40} onChange={(e)=>setFont(e.target.value)} /></abbr>

      <div className={upload?"bulario":"display-none"}>
        {dataArr.length<=0?null:dataArr.map((page)=>{
          return(
          <span key={page.length} style={{fontSize:`${font}px`, width:"80%"}} className='bula-textarea'>
              <p className='bula-paragraph'>
                {textRead += page}
                <Highlighter
                  highlightClassName="HighlightedWords"
                  searchWords={[`${searchedWord}`]}
                  textToHighlight={page}
                />
              </p>
              <br/>
            </span>
          )
        })}
      </div>
      <br />
      <br />
      </div>

    </React.Fragment>
  );
};


export default ReadFile